.CompactCard{
    display: flex;
    flex: 1;
    height: 7rem !important;
    border-radius: .7rem;
    color: white;
    cursor: pointer;
    position: relative;
    padding: 1rem;
}
.CompactCard:hover{
    box-shadow: none !important;
}
.radialBar{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    gap: 1rem;
}
/* circle of percent */
.CircularProgressbar{
    width: 4rem !important;
    overflow: visible;
}
/* circle of percent in inspect of browser */
.CircularProgressbar-path{  
    stroke: white !important;
    stroke-width: 12px !important;
    filter: drop-shadow(2px 4px 6px white);
}
.CircularProgressbar-trail{
    display: none;
}
.CircularProgressbar-text{
    fill: white !important;
}
.radialBar>span{
    font-size: 17px;
    font-weight: bold;
}
/* detail */
.detail{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
}
.detail>span:nth-child(2){
    font-size: 22px;
    font-weight: bold;
}
.detail>span:nth-child(3){
    font-size: 12px;
}
/* Expanded Card*/
.ExpandedCard{
    position: absolute;
    width: 60%;
    height:70vh;
    z-index: 9;
    left: 13rem;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    transition: all 10ms ease-in-out;
}
.ExpandedCard >span:nth-of-type(1){
    color: white;
    font-size: 26px;
    font-weight: bold;
    text-shadow: 0px 0px 15px white;
}
.ExpandedCard >span:nth-of-type(2){
    color: rgb(223, 222, 222);
    font-size: 16px;
    font-weight: bold;
    text-shadow: 0px 0px 5px black;
}
.chartContainer{
    width: 70%;
}
@media screen and (max-width: 1200px){
    .ExpandedCard{
        top: 2rem;
        height: 45vh;
        left: 6rem;
    }
}
@media screen and (max-width: 768px){
    .ExpandedCard{
        top: 8rem;
        height: 50%;
        left: 2.1rem;
        width: 80%;
    }
}